import { warn, webHandleSuccess, webHandleFail } from '../../../common/js'
import SocketTask from './SocketTask'

function connectSocket (options = { url: '' }) {
  const { url, protocols, success, fail, complete } = options

  try {
    const socketTask = new SocketTask(url, protocols)
    webHandleSuccess({ errMsg: 'connectSocket:ok' }, success, complete)
    return socketTask
  } catch (e) {
    webHandleFail({ errMsg: `connectSocket:fail ${e}` }, fail, complete)
  }
}

function sendSocketMessage () {
  warn('sendSocketMessage  socketTask.send')
}

function closeSocket () {
  warn('closeSocket  socketTask.close')
}

function onSocketOpen () {
  warn('onSocketOpen  socketTask.onOpen')
}

function onSocketError () {
  warn('onSocketError  socketTask.onError')
}

function onSocketMessage () {
  warn('onSocketMessage  socketTask.onMessage')
}

function onSocketClose () {
  warn('onSocketClose  socketTask.onClose')
}

export {
  connectSocket,
  sendSocketMessage,
  closeSocket,
  onSocketOpen,
  onSocketError,
  onSocketMessage,
  onSocketClose
}
